.input-data {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
}
.output-data {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-top: 20px;
}
.output-data__text {
  margin: 5px 0;
}
.output-data__list li {
  margin-left: 20px;
  margin-top: 2px;
}
.output-data__list--row {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center;
}