.output-text {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    margin: 5px 0 0 0;
    min-width: 200px;
    width: 350px;
}
.output-text__title {
    margin-right: auto;
    text-align: center;
}
.output-text__value {
    margin-left: 20px;
}