.checkbox-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    max-width: 90%;
    margin-top: 10px;
    text-align: center;
}
.checkbox-container__checkbox {
    margin-right: 10px;
    min-width: 22px;
    min-height: 22px;
    width: 22px;
    height: 22px;
}